<template>
  <div class="register">
    <div class="header"></div>
    <div class="ra">
      <div class="img">
        <van-uploader class="imgu" v-model="imgsrc.fileList" :max-count="1" />
      </div>
    </div>
    <van-cell-group>
      <van-field
        v-model="user.value"
        type="text"
        label="用户名:"
        placeholder="执笔于此"
        :error-message="user.errorMessage"
        @input="validUP(user.value,'validUsername',{key:'user',errorMessage:'用户名支持4-8个(字母数字_)组合，必须字母开头'})"
      />
      <van-field
        v-model="pwd1.value"
        :type="pwd1.type"
        label="密码:"
        placeholder="执笔于此"
        :right-icon="pwd1.icon"
        :error-message="pwd1.errorMessage"
        @click-right-icon="checkPassword('pwd1')"
        @input="validUP(pwd1.value, 'validPwd', {key: 'pwd1', errorMessage: '密码支持6-16个(字母数字_)组合，必须含有大小写字母'})"
      />
      <van-field
        v-model="pwd2.value"
        :type="pwd2.type"
        label="确认密码"
        placeholder="请确认密码"
        :right-icon="pwd2.icon"
        :error-message="pwd2.errorMessage"
        @click-right-icon="checkPassword('pwd2')"
        @input="validEqual(pwd1.value, pwd2.value)"
      />
    </van-cell-group>
    <van-button
      :loading="isLoading"
      type="info"
      loading-text="注册中..."
      round
      block
      text="注册"
      @click="book"
      :disabled="!isValid"
    />
    <!-- 跳到登录页面 -->
    <div class="login">
      <router-link class="login-text active" to="/enroll">已有账号?登录</router-link>
    </div>
  </div>
</template>

<style scoped lang="less">
