  background: none;
  .van-cell {
    margin: 0 20px;
    width: calc(100% - 40px);
    border-radius: 50px;
    margin-bottom: 20px;
    background: rgba(255, 255, 255, 0.75);
    .van-field__label {
      width: 2rem;
      text-align: center;
    }
  }
}
