  margin: 40px 20px 0;
  width: calc(100% - 40px);
}
.van-button--info {
  background-color: #4d5555;
  border: 0.02667rem solid #4d5555;
}
.van-uploader__preview{
  margin: 0;
}
.van-uploader__preview-image{
  border-radius: 50% !important;
}
</style>
<script>
import { validForm } from "../tool/validForm";

import { createNamespacedHelpers } from "vuex";

//基于bookModule的命名空间导入mapState、mapMutations辅助函数

const { mapState, mapMutations } = createNamespacedHelpers("bookModule");

export default {
  name: "register",

  //计算属性
    computed: {
      ...mapState(['pwd1', 'pwd2', 'isLoading', 'isValid', 'user','imgsrc'])
    },	

    //方法
    methods: {
      ...mapMutations(['checkPassword']),

      book() {

        let self = this;

        //注册
        this.$store.commit('bookModule/book', true);

        let randomTime = Math.random() * 5 * 1000;

        setTimeout(function () {

           //获取所有用户信息
          let users = localStorage.getItem('users');

          users = users ? JSON.parse(users) : [];

          // console.log('users ==> ', users);

          //判断当前用户是否存在
          for (let i = 0; i < users.length; i++) {
            if (users[i].username == self.user.value) {
              self.$toast({
                duration: 2000,
                message: '该用户已存在'
              })
              self.$store.commit('bookModule/book', false);
              return;
            }
          }

          self.$toast({
            duration: 2000,
            message: '注册成功'
          })

          self.$store.commit('bookModule/book', false);

          let time = new Date().getTime();

          //记录用户信息
          let userInformation = {
            //用户名
            username: self.user.value,

            //密码
            pwd: self.pwd1.value,

            //注册时间
            time,

            //图片
            imagesrc: self.imgsrc.fileList
          };

          //将当前用户信息推进users
          users.push(userInformation);

          //写入本地存储，模拟数据库
          localStorage.setItem('users', JSON.stringify(users));

        }, randomTime)
      

      },

      //输入验证表单

      //验证用户名
      validUsername(value) {
        // console.log('value ==> ', value);

        let isValid = validForm.validUsername(value);

        this.$store.commit('bookModule/validUsername', isValid)

      },

      //验证密码
      validPwd(value) {
        let isValid = validForm.validPwd(value);

        this.$store.commit('bookModule/validPwd', isValid);
      },

      //验证两值相等
      validEqual(v1, v2) {
        let isValid = validForm.validEqual(v1, v2);

        this.$store.commit('bookModule/validEqual', isValid);

        this.$store.commit('bookModule/passForm');
      },

      //验证用户名或者密码
      validUP(value, fnName, o) {
        let isValid = validForm[fnName](value);

        // console.log('isValid ==> ', isValid);

        o.isValid = isValid;

        this.$store.commit('bookModule/validUP', o);

        this.$store.commit('bookModule/passForm');
      },
    
  }
};
</script>
